import { Component, OnInit } from '@angular/core';
import { MenuService } from 'src/app/services/menu.service';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { ServiceService } from './vendor-service/service.service';
import { VendorVendorService } from 'src/app/sdk/asset/services';
import * as _ from 'lodash';
import { filter } from 'rxjs/operators';

@Component({
    selector: 'app-detail-vendor',
    templateUrl: './detail-vendor.component.html',
    styleUrls: ['./detail-vendor.component.scss']
})
export class DetailVendorComponent implements OnInit {

    menuData: any = []
    isMobileView: boolean
    isMobileMenuOpen: boolean
    isMenuCollapsed: boolean
    isMenuUnfixed: boolean
    isMenuShadow: boolean
    isSidebarOpen: boolean
    menuType: string
    menuColor: string
    flyoutMenuColor: string
    menuLayoutType: string

    activeSubmenu: string = ''
    activeItem: string = ''
    renderedFlyoutItems: object = {}
    flyoutTimers: object = {}
    flyoutActive: boolean = false
    objectKeys = Object.keys

    constructor(
        private menuService: MenuService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private serviceService: ServiceService,
        private vendorVendorService: VendorVendorService,

    ) { }

    id_vendor: string = ''


    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(params => {
            if (params.get('id_vendor')) {
                this.id_vendor = params.get('id_vendor')
                this.serviceService.setIdVendor(this.id_vendor)
            }

            this.getVendor()

        })


        this.menuData = [
            {
                title: 'Details',
                key: 'detail',
                icon: 'fe fe-bookmark',
                url: 'detail',
            },

            {
                title: 'Riwayat',
                key: 'Lainnya',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'Purchase Order',
                        key: 'po',
                        icon: 'fe fe-bookmark',
                        url: 'po',
                    },
                    {
                        title: 'Audit',
                        key: 'audit',
                        icon: 'fe fe-bookmark',
                        url: 'audit',
                    }

                ],
            },
            {
                title: 'Contact',
                key: 'contact',
                icon: 'fe fe-bookmark',
                url: 'contact',
            },
            {
                title: 'Category',
                key: 'kategori',
                icon: 'fe fe-bookmark',
                url: 'kategori',
            },

        ]

        // this.setActiveItems(this.router.url)
        this.router.events
            .pipe(filter(event => event instanceof NavigationStart))
            .subscribe((event: NavigationStart) => {
                // this.setActiveItems(event.url ? event.url : null)
            })
    }



    toggleMobileMenu() { }

    toggleMenu() { }

    toggleSettings() { }

    handleSubmenuClick(key: string) {
        const currentKey = this.activeSubmenu
        if (this.flyoutActive) {
            return
        }
        this.activeSubmenu = currentKey === key ? '' : key
    }

    setActiveItems(pathname) {
        const menuData = this.menuData
        const flattenItems = (items, key) =>
            items.reduce((flattenedItems, item) => {
                flattenedItems.push(item)
                if (Array.isArray(item[key])) {
                    return flattenedItems.concat(flattenItems(item[key], key))
                }
                return flattenedItems
            }, [])
        const activeItem = _.find(flattenItems(menuData, 'children'), ['url', pathname])
        const activeSubmenu = menuData.reduce((key, parent) => {
            if (Array.isArray(parent.children)) {
                parent.children.map(child => {
                    if (child.key === activeItem.key) {
                        key = parent
                    }
                    return ''
                })
            }
            return key
        })

        this.activeItem = activeItem.key
        this.activeSubmenu = activeSubmenu.key
    }

    handleFlyoutOver(event, key, items) {
        if (this.flyoutActive) {
            clearInterval(this.flyoutTimers[key])
            const item = event.currentTarget
            const itemDimensions = item.getBoundingClientRect()
            this.renderedFlyoutItems = {
                ...this.renderedFlyoutItems,
                [key]: {
                    key,
                    itemDimensions,
                    items,
                },
            }
        }
    }

    handleFlyoutOut(key) {
        if (this.flyoutActive) {
            this.flyoutTimers[key] = setTimeout(() => {
                const updatedFlyoutItems = Object.assign({}, this.renderedFlyoutItems)
                delete updatedFlyoutItems[key]
                this.renderedFlyoutItems = {
                    ...updatedFlyoutItems,
                }
            }, 100)
        }
    }

    handleFlyoutContainerOver(key) {
        clearInterval(this.flyoutTimers[key])
    }

    dataVendor: any = {}
    getVendor(){
        this.vendorVendorService.getVendorVendorsId(this.id_vendor).subscribe(
            data=> {
                this.dataVendor = data.data
            }
        )
    }

}
