import { Component, OnInit } from '@angular/core';
import { VendorVendorAuditService } from 'src/app/sdk/asset/services';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Vendor_audit } from 'src/app/sdk/asset/models';
import { ServiceService } from '../../vendor-service/service.service';

@Component({
  selector: 'app-audit',
  templateUrl: './audit.component.html',
  styleUrls: ['./audit.component.scss']
})
export class AuditComponent implements OnInit {

    constructor(
        private vendorVendorAuditService: VendorVendorAuditService,
        private serviceService: ServiceService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    id_vendor: string = ''
        ngOnInit() {
            this.serviceService.getIdVendor().subscribe(data => {
                this.id_vendor = data
            })
            this.getData()
        }

    listOfDisplayData: Vendor_audit[] = []
    listOfData: Vendor_audit[] = []
    getData() {
        this.spinner.show()
        this.vendorVendorAuditService.getVendorVendorAudits(JSON.stringify({id_vendor: this.id_vendor, sortKey: this.sortKey, sortValue: this.sortValue,})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()
            }
        )
    }

    sortKey: string | null = null;
    sortValue: string | null = null;
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }


    // delete(id) {
    //     this.spinner.show();
    //     this.vendorVendorAuditService.deleteVendorVendorAuditsId(id).subscribe(
    //         data => {
    //             this.getData();
    //             this.spinner.hide();
    //         }
    //     )
    // }

    // batalDelete() {
    //     this.nzMessageService.info('click cancel');
    // }

}
