import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseOrderService } from 'src/app/sdk/purchasing/services';
import { Purchase_order } from 'src/app/sdk/purchasing/models';
import { ServiceService } from '../../vendor-service/service.service';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { GaPembelianService } from 'src/app/sdk/hr/services';
import { Pembelian } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-po',
  templateUrl: './po.component.html',
  styleUrls: ['./po.component.scss']
})
export class PoComponent implements OnInit {

    constructor(
        private gaPembelianService: GaPembelianService,
        private serviceService: ServiceService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    id_vendor: string = ''
    ngOnInit() {
        this.serviceService.getIdVendor().subscribe(data => {
            this.id_vendor = data
        })
        this.searchData()
    }


    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.gaPembelianService
            .getGaPembelians(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search, id_vendor:this.id_vendor
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.listOfData = data.data.data;
                this.total = data.data.total;

            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }




    delete(id) {
        this.spinner.show();
        this.gaPembelianService.deleteGaPembeliansId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }

}
