import { Component, OnInit } from '@angular/core';
import { VendorVendorKategoriMasterService, VendorVendorKategoriService } from 'src/app/sdk/asset/services';
import { ServiceService } from '../vendor-service/service.service';
import { Vendor_kategori, Vendor_kategori_master } from 'src/app/sdk/asset/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';


@Component({
    selector: 'app-kategori',
    templateUrl: './kategori.component.html',
    styleUrls: ['./kategori.component.scss']
})
export class KategoriComponent implements OnInit {

    constructor(
        private vendorVendorKategoriMasterService: VendorVendorKategoriMasterService,
        private vendorVendorKategoriService: VendorVendorKategoriService,
        private serviceService: ServiceService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,

    ) { }


    listCategory: Vendor_kategori[] = []
    listMaster: Vendor_kategori_master[] = []
    id_vendor: string = null

    toogleAdd = false

    ngOnInit() {
        this.serviceService.getIdVendor().subscribe(
            data => {
                if (data) {
                    this.id_vendor = data
                    this.getListKategori()
                    this.getMaster()
                }
            }
        )
    }

    toogleAddShow() {
        this.toogleAdd = true
    }

    getListKategori() {
        this.vendorVendorKategoriService.getVendorVendorKategoris(JSON.stringify({ id_vendor: this.id_vendor })).subscribe(
            data => {
                this.listCategory = data.data
            }
        )
    }

    getMaster() {
        this.vendorVendorKategoriMasterService.getVendorVendorKategoriMasters().subscribe(
            data => {
                this.listMaster = data.data
            }
        )
    }


    deleteItem(id) {
        this.spinner.show()
        this.vendorVendorKategoriService.deleteVendorVendorKategorisId(id).subscribe(
            data => {
                this.getListKategori()
                this.spinner.hide()
            }
        )
    }


    id_vendor_kategori_master: string = ''
    simpan_item() {

        let param: Vendor_kategori = {
            id_vendor: this.id_vendor,
            id_vendor_kategori_master: this.id_vendor_kategori_master
        };

        this.spinner.show()
        this.vendorVendorKategoriService.postVendorVendorKategoris(param).subscribe(
            data => {
                this.toogleAdd = false
                this.getListKategori()
                this.spinner.hide();
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

}
