import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DetailVendorComponent } from './detail-vendor.component';
import { DetailComponent } from './detail/detail.component';
import { PoComponent } from './riwayat/po/po.component';
import { KategoriComponent } from './kategori/kategori.component';
import { AuditComponent } from './riwayat/audit/audit.component';
import { DetailPoComponent } from './riwayat/detail-po/detail-po.component';


const routes: Routes = [
    {
        path: '',
        component: DetailVendorComponent,
        children: [
            {
                path: '',
                redirectTo: 'detail'
            },
            {
                path: 'detail',
                component: DetailComponent
            },
            {
                path: 'po',
                component: PoComponent
            },
            {
                path: 'detail-po/:id',
                component: DetailPoComponent
            },
            {
                path: 'audit',
                component: AuditComponent
            },
            {
                path: 'kategori',
                component: KategoriComponent
            },
            {
                path: 'contact',
                loadChildren: () =>
                    import('./contact/contact.module').then(mod => mod.ContactModule),
            },

        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DetailVendorRoutingModule { }
