import { Component, OnInit } from '@angular/core';
import { Vendor, Part_id, Life_limit, Condition_monitoring, Manufacture } from 'src/app/sdk/asset/models';
import { VendorVendorService } from 'src/app/sdk/asset/services';
import { ServiceService } from '../vendor-service/service.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private vendorVendorService: VendorVendorService,
        private serviceService: ServiceService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,

    ) { }


    dataVendor: Vendor= {}

    id_vendor: string = ''


    ngOnInit() {

        this.serviceService.getIdVendor().subscribe(data => {
            this.id_vendor = data

            this.getData()
        })
    }


    getData() {
        this.spinner.show()
        this.vendorVendorService.getVendorVendorsId(this.id_vendor).subscribe(
            data => {
                this.dataVendor = data.data;
                this.spinner.hide()

            }
        )
    }

}
